* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body{
    background: var(--color-bg);
}

a {
    color: unset;
    text-decoration: none;
}

.gradient__bg{
    /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 0% 40%, rgba(14, 32, 66, 1) 0%, rgba(4, 12, 24, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 0% 40%, rgba(14, 32, 66, 1) 0%, rgba(4, 12, 24, 1) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 0% 40%, rgba(14, 32, 66, 1) 0%, rgba(4, 12, 24, 1) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(circle at 0% 40%, rgba(14, 32, 66, 1) 0%, rgba(4, 12, 24, 1) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 0% 40%, rgba(14, 32, 66, 1) 0%, rgba(4, 12, 24, 1) 100%);

    height: 100vh;

}

.slide-bck-center{animation:slide-bck-center .45s cubic-bezier(.47,0.000,.745,.715) both}

@keyframes slide-bck-center{0%{transform:translateZ(0)}100%{transform:translateZ(-400px)}}

.tracking-in-expand{animation:tracking-in-expand 2.5s cubic-bezier(.215,.61,.355,1.000) both}
@keyframes tracking-in-expand{0%{letter-spacing:-.5em;opacity:0}40%{opacity:.6}100%{opacity:1}}
