.matchup__bg{
    display: flex;
    flex-direction: column;

}
.matchup__teams{
    align-items: center;
    text-align: center;
    display: flex;
    justify-content: center;
}
.matchup__logo{
    width: 300px;
}
.matchup__teams img{
    width: 120px;
}

.matchup__teams h2{
    -webkit-animation: tracking-in-expand 2.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
    animation: tracking-in-expand 2.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
    background: #FF540A;
    background: -webkit-linear-gradient(to right, #FF540A 23%, #FFC72C 100%);
    background: -moz-linear-gradient(to right, #FF540A 23%, #FFC72C 100%);
    background: linear-gradient(to right, #FF540A 23%, #FFC72C 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

}
